.App-header {
    box-shadow: 0px 0px 20px 1px rgba(0, 0, 0, 0.040);
    min-height: 100px;  
    display: flex;
    justify-content: center;
    font-size: calc(3px + 2vmin);
    color: white;
    letter-spacing: 1px;
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 1000;
}

.Header-body{
    display: flex;
    min-width: 750px;
    width: 1000px;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    text-shadow: 0px 5px 10px rgba(0, 0, 0, 0.30);
    
}
.Header-left{
    display: flex;
    align-items: center;
}
.Header-logo{
    font-size: calc(10px + 2vmin);
    font-weight: 600;
}
.Header-menu{
    margin: 0px;
}
.Menu-item{
    margin-left: 30px;
    margin-right: 10px;
    opacity: 0.8;
    transition: 0.2s;
}
.Menu-item.active{
    opacity: 2;
    font-weight: 600;
    color: #725eff;
    text-shadow: 0px 5px 10px #715eff90;
}
.Menu-item:hover{
    opacity: 2;
    color: #725eff;
    font-weight: 600;
    transition: 0.2s;
}

.User-action-item{
    margin-left: 10px;
   padding: 7px 10px;

    font-size: 20px;

}
.User-action-item.active{
   
    border-radius: 100px;
    background-color: rgba(0, 0, 0, 0.10);
    
}

.User-action-item:hover{
    border-radius: 100px;
    background-color: rgba(0, 0, 0, 0.10);
}