
.second-nav{
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.010);
    width: 100%;
    
    align-items: center;
    margin-top: 100px;
}
.second-menu{
    
    margin: 0px;
    display: flex;
    min-width: 750px;
    width: 1000px;
    height: 8vh;
    padding: 0px 20px 0px 20px;
}
.second-menuitem{
    
   
    font-size: calc(6px + 1vmin);
   margin-right: 20px;
   padding-top: 15px;
   padding-bottom: 15px;
    opacity: 0.7;
    line-height: 30px;
}
.second-menuitem:hover{
    opacity: 2;
    font-weight: 500;
    border-bottom: 2px solid #725eff;
    color: #725eff;
}
.second-menuitem.active{
    opacity: 2;
    font-weight: 500;
    border-bottom: 2px solid #725eff;
    color: #725eff;
}



main{
    display: flex;
    flex-direction: column;
}


.devloping{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    background-color: #00000005;
}