.Header-search{
    display: flex;
    align-items: center;
}

input{
    border-radius: 20px;
    height: 30px;
    width: 250px;
    letter-spacing: 1px;
    padding-left: 40px;
    border-width: 0px;
    background-color: rgba(0, 0, 0, 0.040);
    opacity: 0.8;
    transition: 0.3s;
}
input:hover{
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.20);
    opacity: 1;
    transition: 0.3s;
}

.Search-icon{
    color: black;
    font-size: 20px;
    
    position: relative;
    right: 280px;
    opacity: 0.5;
}